<template>
  <div class="score">
    <van-nav-bar title="成绩查询" left-text="返回" right-text="" left-arrow @click-left="handleClickLeft" />
    <van-row gutter="20" v-if="showScore">
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.id" placeholder="请输入准考证号" label="准考证号" required/>
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="请输入姓名" label="姓名" required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-button type="primary" bottom-action class="btn" @click="GetScore" :disabled="disabled">确定</van-button>
      </van-col>

    </van-row>

    <van-row gutter="20" v-else>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="score.id" placeholder="" label="准考证号" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="姓名" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="score.total" placeholder="" label="总分" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="score.chinese" placeholder="" label="语文" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="score.math" placeholder="" label="数学" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="score.mixed" placeholder="" label="综合" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-button type="primary" bottom-action class="btn" @click="$router.push({name: 'Home'})">关闭</van-button>
      </van-col>

    </van-row>

  </div>
</template>


<script>
export default {
  data() {
    return {
      showScore: true,
      form: {
        id: '',
        name: ''
      },
      loading: false,
      disabled: true,
      score: {}
    }
  },
  methods: {
    handleClickLeft() {
      if (this.showScore) {
        this.$router.back()
        return
      }

      this.form = {
        id: '',
        name: ''
      }
      this.showScore = true
    },
    GetScore() {
      const vm = this
      const params = {
        name: vm.form.name,
        id: vm.form.id
      }

      // vm.$store.commit(vm.$types.ShowLoading, true)
      vm.$toast.fail(`暂无数据`)
      // vm.$api
      //   .GetScore(params)
      //   .then(res => {
      //     if (res.data.code == 200) {
      //       vm.showScore = false
      //       vm.score = res.data.data
      //     } else {
      //       vm.$toast.fail(`${res.data.message}`)
      //     }
      //     vm.$store.commit(vm.$types.ShowLoading, false)
      //   })
      //   .catch(err => {
      //     vm.$toast.fail(JSON.stringify(err))
      //     vm.$store.commit(vm.$types.ShowLoading, false)
      //   })
    }
  },

  watch: {
    form: {
      deep: true,
      handler(val, oldVal) {
        if (val.id !== '' && val.name !== '') {
          this.disabled = false
        } else {
          this.disabled = true
        }
      },
      immediate: true
    }
  },

  created() {}
}
</script>

<style lang="scss" scoped>
.score {
  // height: 100vh;
  .btn {
    margin-top: 20px;
  }
}
</style>




